<template>
	<div id="shade" @click="hide">
        <p>点击右上角<br>选择【在浏览器打开】进行下载</p>
        <!-- <img id='img' src="../../../../assets/weixin-tip.png" alt="pic"> -->
        <span class="weixin-icon"></span>
    </div>
</template>
<script>
	export default {
		name:'dowloan',
		methods:{
			hide(){
				this.$store.state.show_download = false;
			}
		}
	}
</script>
<style>
	#shade{
            position: relative;
            /*水平居中*/
            text-align: center;
            width: 100%;
            height: 100%;
            background-color:rgba(0,0,0,0.5);
        }
        #shade p{
            position: absolute;
            color: white;
            font-size: .4rem;
            text-align: center;
            top:20%;
            line-height: .55rem;
            width: 100%;
        }
        #shade .weixin-icon{
    	    width: 52px;
		    height: 67px;
		    background: url(../../../../assets/weixin-tip.png) no-repeat;
		    display: block;
		    position: absolute;
		    right: 30px;
		    top: 20px;
        }
</style>


