<template>
    <div class="follow">
        <q-header :set="true" :text="'下载中心'"></q-header>
        <div v-title data-title="关注公众号">找到我们</div>
        
        <div class="logo">
          <img :src="logo" alt="">
        </div>
   
        <div v-if="platform">
          <img class="code" :src="img" alt="">
          <p class="tip">识别二维码，关注公众号</p>
        </div>
        <div v-else>
          <img class="code" :src="qinglogo" alt="">
        </div>

       <div class="box">
            <p v-if="!off" v-show="platform" @click="goShopping">前往商城</p>
            <p v-else @click="goGoods">继续购物</p>
            <p @click="app">点击下载APP</p>
       </div>
       <div class="phone" v-show="this.phone">
        <p > 咨询电话：{{this.phone}}</p>
       </div>
       <div v-show="$store.state.show_download" style="width:100%;height:100%;position: fixed;left:0;top:0;">
        <down-load></down-load>
      </div>
    </div>
    
</template>
<script>
import { Toast } from 'mint-ui';  
import qHeader from '@/components/page/children/header';
import downLoad from '../home/children/children/downLoad'
    export default {
        name: 'follow',
        data() {
            return {
               img:require('@/assets/WechatIMG44.jpeg'),
               logo:require('@/assets/logo.png'),
               qinglogo:require('@/assets/qing.jpg'),
               off:true,
               platform:true,
               phone:'',

            }
        },
        methods:{
            goShopping(){
                this.$router.push('/home');
            },
            goGoods(){
                this.$router.push({
                  name:'product',
                  params:{
                      id:this.off,
                      status:1
                  }
                });
            },
             app(){
                var ua = window.navigator.userAgent.toLowerCase();
                if(ua.match(/MicroMessenger/i) == 'micromessenger'){
                    this.$store.state.show_download = true;
                }else{
                    var u = navigator.userAgent, app = navigator.appVersion;
                    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
                    var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                    if (isAndroid) {
                       //这个是安卓操作系统
                       Toast('可能需要进入手机“文件管理”找到APP并安装');
                       window.open( "http://www.2tianqc.com/App/2tianqc_a.apk");
                    }
                    if (isIOS) {
                　　　　//这个是ios操作系统
                        Toast('请到苹果App Store搜索ShopsN');
                         window.open( "https://itunes.apple.com/cn/app/storybear/id1374901365");
                    } 
                }
                
                
            }
        },
        created(){
          this.off = sessionStorage.getItem('goods_id')?sessionStorage.getItem('goods_id'):false;
          
          var ua = window.navigator.userAgent.toLowerCase();
          if(ua.match(/MicroMessenger/i) == 'micromessenger'){
              this.platform = true;
          }else{
              this.platform = false;
          }

          this.axios.post(API_URL + 'Home/Index/getPhone').then((res) => {
                this.phone = res.data.data;
            }).catch((err) => {
                console.log(err);
            });

        },
        components:{
          downLoad,
          qHeader
        }

    }
</script>
<style lang="less" scoped>
    .follow{
       position: relative;
       top: 0;
       left: 0;
       right: 0;
       bottom: 0;
       background: #fff;
       width: 100%;
       height: 100%;
       text-align: center;
       .logo{
           padding-top: .5rem;
           img{
            width: 1.5rem;
            height: .5rem;
           }
       }
       .code{
           width: 3rem;
           height: 3rem;
           padding-top: 2rem;
       }
       .tip{
           padding-top:.1rem;
           font-size: .2rem;
       }
       .box{
           text-align: center;
           p{
                display: inline-block;
                padding:.78rem;
                font-size:.33rem; 
                font-weight: 600;
                text-decoration: underline;
           }
       }
       .phone{
           text-align: center;
           p{
                display: inline-block;
                padding:.78rem;
                font-size:.33rem; 
                font-weight: 600;
                text-decoration: underline;
           }
       }
    }
</style>


